.photo-tabs {
    border-bottom: 1px solid var(--border);
  }
  
  .photo-tab {
    padding: 0.75rem 1.25rem;
    color: var(--text-gray);
    font-weight: 500;
    border: none;
    background: none;
    position: relative;
    cursor: pointer;
  }
  
  .photo-tab::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 3px;
    background-color: var(--primary);
    transition: var(--transition);
  }
  
  .photo-tab.active {
    color: var(--primary);
  }
  
  .photo-tab.active::after {
    width: 100%;
  }
  
  .photo-tab:hover:not(.active) {
    color: var(--text-dark);
  }